body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0; }

.app {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between; }

.navbar {
  text-align: center;
  background-color: #5a537a;
  margin: 0;
  padding: 10px 0; }
  .navbar__heading {
    color: #FFFFFF;
    text-transform: uppercase; }

.dashboard {
  display: flex;
  flex-direction: column; }

.footer {
  text-align: center;
  background-color: #5a537a;
  padding: 20px 0;
  color: #dddddd;
  text-transform: uppercase;
  font-weight: 700; }

.list {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow-x: auto; }
  .list__table {
    text-align: center; }

th,
td {
  padding: 10px;
  border-bottom: 1px solid #dddddd; }

.loading {
  text-align: center; }

.button {
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  font-weight: 600;
  border: 1px solid;
  border-radius: 10px;
  display: flex;
  align-items: center;
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.19); }
  .button--add {
    background-color: #22FF7D;
    align-self: center;
    margin: 10px 0;
    border-color: #0dd760;
    padding: 10px 30px; }
    .button--add:active {
      background-color: #0dd760; }
    .button--add:hover .icon--add {
      -webkit-transform: translateX(-25px);
              transform: translateX(-25px);
      opacity: 1; }
  .button--edit {
    background-color: #FFC422;
    padding: 4px 15px;
    border-color: #f8b500; }
  .button--delete {
    background-color: #FF2237;
    padding: 7px 15px;
    border-color: #c00012; }
  .button--submit {
    align-self: center;
    margin: 10px 0;
    background-color: #022c68;
    color: #dddddd;
    padding: 7px 17px; }
  .button--back {
    padding: 4px 25px;
    margin: 10px 0; }
    .button--back:hover .icon--back {
      -webkit-transform: translateX(-20px);
              transform: translateX(-20px);
      opacity: 0; }

.icon {
  padding-right: 5px; }
  .icon--add {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
    opacity: 0;
    transition: all .3s;
    position: absolute; }
  .icon--back {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
    opacity: 1;
    transition: all .3s;
    position: absolute; }

.form {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .form__container {
    display: flex;
    flex-direction: column; }
  .form__group {
    display: flex;
    justify-content: space-between;
    margin: 5px 0; }
  .form__input {
    margin: 0 5px;
    padding: 5px; }

.pagination__list {
  list-style: none;
  display: flex;
  margin: 10px 0;
  padding: 0; }

.pagination__item {
  cursor: pointer;
  padding: 5px 10px;
  border: 1px solid #5a537a; }
  .pagination__item--active {
    background-color: #5a537a; }
  .pagination__item:hover {
    background-color: #dddddd; }

.pagination__link {
  text-decoration: none;
  color: #000000;
  font-weight: 700; }

